import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Properties, User, Api } from '../../providers';


@IonicPage()
@Component({
  selector: 'page-my-properties',
  templateUrl: 'my-properties.html',
})
export class MyPropertiesPage {
  public item: any;
  public cardItems: any = [];
  public menu: boolean = false;
  constructor(public user: User, public api: Api, public navCtrl: NavController, public navParams: NavParams, public properties: Properties) {
    console.log(user.userInfo.global);
  }

  ionViewDidLoad() {

  }

  ionViewCanEnter() {

    //Filtra - valida propiedades
    this.loadProperties();
  }


  initializeItems() {
    this.item = this.properties.userProperties;
  }

  detail(folio) {

    this.properties.getPreview(folio).subscribe(data => {
      this.properties.property = data.json().data;
      this.navCtrl.push('PropertyDetailPage', 2);

    });


  }

  edit(folio) {

    this.properties.query(folio + '&preview=true&token=' + this.user.userToken + '&user=' + this.user.userInfo.userid).subscribe(data => {
      this.properties.property = data.json().data;

      this.navCtrl.push('CreatePropertyPage', folio);

    });

  }

  doRefresh(event) {
    var properties = this.properties.getUserProperties();
    Promise.all([properties]).then(data => {
      event.complete();
      this.item = this.properties.userProperties;

    });
  }

  getItems(ev: any) {

    this.initializeItems();
    const val = ev.target.value;


    if (val && val.trim() != '') {


      this.item = this.item.filter((item) => {

        if (item.namePropertyEs.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;
        } else if (item.folio.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;

        } else if (item.operation.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;

        } else if (item.city.toLowerCase().indexOf(val.toLowerCase()) > -1) {
          return true;

        }


        //    return (item.namePropertyEs.toLowerCase().indexOf(val.toLowerCase()) > -1);

      })


    }



  }

  addItem() {

    this.navCtrl.push('CreatePropertyPage');

  }

  filtre() {
    this.navCtrl.push('FiltrePage', { page: 'userProperties' });
  }


  loadProperties() {


    this.cardItems = [];
    var $this = this;
    this.properties.userProperties.forEach((value, key, index) => {



      var integer = 0;
      var integer2 = 0;

      if (value.descriptionEs.length > 130) {
        value.descriptionEs = value.descriptionEs.substring(0, 145);
        value.descriptionEs = value.descriptionEs + '...';
      }



      //Oficina
      if (this.api.filtreUserProperties.officeid) {
        if (!(this.api.filtreUserProperties.officeid == value.officeidsold)) {
          return;
        }
      }

      //tipo de propiedad
      if (this.api.filtreUserProperties.type) {
        if (!(this.api.filtreUserProperties.type == value.typepropertyid)) {
          return;
        }
      }

      //tipo de operacion
      if (this.api.filtreUserProperties.operation) {
        if (!(this.api.filtreUserProperties.operation == value.operation)) {
          return;
        }
      }

      //Estatus de la propiedad
      if (this.api.filtreUserProperties.status) {

        if (this.api.filtreUserProperties.status == 1) {

          if (value.statusid != 1) {
            return;
          }

        } else {

          if (value.statusid == 1) {
            return;
          }

        }

      }

      //Desarrollo
      integer = parseInt(this.api.filtreUserProperties.development, 10);
      integer2 = parseInt(value.num_desarrollo, 10);

      if (this.api.filtreUserProperties.development) {
        if (!(integer2 == integer)) {
          return;
        }
      }

      //Tipo de moneda
      integer = parseInt(this.api.filtreUserProperties.moneda, 10);
      integer2 = parseInt(value.currencyid, 10);

      if (this.api.filtreUserProperties.moneda) {
        if (!(integer2 == integer)) {
          return;
        }
      }
      //-

      //Precio minimo
      integer = parseInt(this.api.filtreUserProperties.priceMin, 10);
      integer2 = parseInt(value.price, 10);

      if (this.api.filtreUserProperties.priceMin && value.price) {
        if (!(integer <= integer2)) {
          return;
        }
      }
      //-

      //Precio maximo
      integer = parseInt(this.api.filtreUserProperties.priceMax, 10);
      integer2 = parseInt(value.price, 10);

      if (this.api.filtreUserProperties.priceMax && value.price) {
        if (!(integer >= integer2)) {
          return;
        }
      }
      //-

      //Recamaras minimas
      integer = parseInt(this.api.filtreUserProperties.bedMin, 10);
      integer2 = parseInt(value.bedrooms, 10);
      if (this.api.filtreUserProperties.bedMin && value.bedrooms) {
        if (!(integer <= integer2)) {
          return;
        }
      }
      //-

      //Recamaras maximas
      integer = parseInt(this.api.filtreUserProperties.bedMax, 10);
      integer2 = parseInt(value.bedrooms, 10);

      if (this.api.filtreUserProperties.bedMax && value.bedrooms) {
        if (!(integer >= integer2)) {
          return;
        }
      }
      //-

      //Metros 2 minimos
      integer = parseInt(this.api.filtreUserProperties.m2Min, 10);
      integer2 = parseInt(value.m2c, 10);

      if (this.api.filtreUserProperties.m2Min && value.m2c) {

        if (!(integer <= integer2)) {
          return;
        }
      }
      //-

      //Metros 2 Maximos
      integer = parseInt(this.api.filtreUserProperties.m2Max, 10);
      integer2 = parseInt(value.m2c, 10);

      if (this.api.filtreUserProperties.m2Max && value.m2c) {

        if (!(integer >= integer2)) {
          return;
        }
      }

      $this.cardItems.push(value);


    });

    this.item = this.cardItems;
  }

}



